html {
  scroll-behavior: smooth;
}

body, html {
  margin: 0px;
}

body {
  font-family: Karla, -apple-system, BlinkMacSystemFont, 'San Francisco', 'Segoe UI', 'Helvetica Neue', sans-serif;
  font-size: 1rem;
  line-height: 1.5rem;
  --tw-text-opacity: 1;
  color: rgba(var(--adjust-tone-04), var(--tw-text-opacity));
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  text-rendering: optimizeLegibility;
  -webkit-text-size-adjust: 100%;
     -moz-text-size-adjust: 100%;
          text-size-adjust: 100%;
}

button, optgroup, select {
  font-family: Roboto, -apple-system, BlinkMacSystemFont, 'San Francisco', 'Segoe UI', 'Helvetica Neue', sans-serif;
  font-weight: 500;
  line-height: 1.625;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input, textarea {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  font-family: inherit;
}

b, strong {
  font-weight: 600;
  letter-spacing: -0.025em;
}

a {
  --tw-text-opacity: 1;
  color: rgba(var(--brand-maggioli-03), var(--tw-text-opacity));
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

a, aside, body, code, div, datalist, footer, form, h1, h2, h3, h4, h5, h6, header, hr, html, input, li, main, section, span, ul, textarea {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0px;
}

::-moz-selection {
  --tw-bg-opacity: 1;
  background-color: rgba(var(--brand-maggioli-09), var(--tw-bg-opacity));
  --tw-text-opacity: 1;
  color: rgba(var(--brand-maggioli-02), var(--tw-text-opacity));
}

::selection {
  --tw-bg-opacity: 1;
  background-color: rgba(var(--brand-maggioli-09), var(--tw-bg-opacity));
  --tw-text-opacity: 1;
  color: rgba(var(--brand-maggioli-02), var(--tw-text-opacity));
}
